import React, { Component } from 'react';
import EmptyWithIcon from '../CommonComponents/Empty/EmptyWithIcon';
import withRouter from '../../../routes/WithRouter';

class PageNotFound extends Component {
       render() {
              const pageContent = window?.APP_CONTENT?.no_page_found ?? {};
              return (
                     <div className="f-center" style={{minHeight:'calc(100vh - 160px)'}}>
                            <EmptyWithIcon title={pageContent?.title ?? ""} desc={pageContent?.desc ?? ""} icon={'user-ban'} button={{ text: pageContent?.button ?? "", action: ()=>this.props.navigate(`\home`) }} isFullScreen={false} />
                     </div>
              );
       }
}

export default withRouter(PageNotFound);